.tall {
  min-height: 70vh;
}

.margined {
  margin: 1em;
}

.spaced {
  width: 50vh;
  margin-left: 0.5em;
  margin-right: 0.5em;
}

.trash {
  position: absolute;
  padding: 5px;
  margin-top: 0.3em;
  margin-left: 0.6em;
}

.grey {
  color: grey;
}

.unpadded {
  padding: 5px;
  margin-right: 0.3em;
}

:invalid {
  border: tomato solid 3px;
}

.outlined {
  border: solid 2px;
  border-radius: 10em;
  margin-left: 0;
  padding-right: 0.3em;
}

.section {
  margin: 1em 0;
}

.summary {
  font-size: large;
  font-weight: bold;
  cursor: pointer;
}

.aligned {
  display: flex;
  align-items: center;
  margin: 0.5em;
}

.button {
  text-align: right;
  margin-top: 1em;
}

.search {
  margin-right: 1em;
}
